// WEB3 > JS
import Web3 from "web3";
import ABI from "./abi.json";


let web3 = [],  Contract = [], myWeb3 = [];


async function loadData(){
    localStorage.removeItem("wallet")
    myWeb3 = await new Web3("https://ropsten.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161");
    await contract()
    return "loaded"
}


async function connectWallet(e) {
    let a1;

    if(window.ethereum){
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        console.log(chainId);
        if (chainId !== "0x3") {
            alert("Connect to correct network")
        
        } else {
    
            a1 =  await window.ethereum.enable()
            .then((account1) => {
                console.log(account1);
                return account1
            }).catch((err) => {
                if(err.code === 4001) {
                    alert("User rejected the request.")

                } else if(err.code === -32602){
                    alert("Please connect your registered wallet address or reload page.")

                } else {
                    alert(err.message.split(":")[0])

                }
            });
            myWeb3 = new Web3(window.ethereum);
            console.log(myWeb3);
            contract()

        }
    
        console.log(myWeb3);
        console.log("a1");
        console.log(a1);
        if(typeof  a1 != "undefined"){
            localStorage.setItem("wallet", JSON.stringify([a1[0], true]));
            return a1[0];
        } else {
            return "CONNECT WALLET"
        }

    } else {
        alert("Install Metamask Wallet to continue.")
    }


}

async function myAccount() {
    const account = await myWeb3.eth.getAccounts()

    console.log(account);
    return account[0];
}


async function contract() {
    Contract = await new myWeb3.eth.Contract(ABI, "0xADFE7A6ae68bE8E0606329172BD5bb604343b050");
    console.log(Contract);
}

// Call Functions
async function nftPrice() {
    return await Contract.methods._price().call();
}

async function maxTx() {
    return await Contract.methods._maxperTX().call();
}

async function Limit() {
    return await Contract.methods._limit().call();
}

async function TotalSupply() {
    return await Contract.methods.totalSupply().call();
}



// Send Functions
async function MintNow(num) {
    if(typeof window.ethereum !== "undefined"){
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        console.log(chainId);
        if (chainId !== "0x3") {
            alert("Connect to correct network")
        
        } else {
            const connected = JSON.parse(localStorage.getItem("wallet"));
            console.log(connected);
            if(typeof connected !== "undefined" && connected  !==  null && connected[1] === true){
                const account = await myAccount();
                const __price = await nftPrice();
        
                await Contract.methods.MINT(num).send({ from: account, value: (parseInt(__price) * num) })
                .then((result) => {
                    alert("Mint successfull")
                
                }).catch((err) => {
                    console.log(err);
                    
                });

            } else {
                alert("Connect your wallet first");

            }
        }

    } else {
        alert("Install Metamask Wallet to continue.")
    }
}

if( typeof window.ethereum !== "undefined"){
    window.ethereum.on("chainChanged", () => {
        connectWallet()
    })
}



let a =  {
    web3: web3,
    loadData: loadData, 
    connectWallet: connectWallet,
    myAccount: myAccount,
    nftPrice: nftPrice,
    maxTx: maxTx,
    Limit: Limit,
    TotalSupply: TotalSupply,
    MintNow: MintNow,
}



// 0xADFE7A6ae68bE8E0606329172BD5bb604343b050


export default a;